import * as React from 'react'
import { Link, type HeadFC, type PageProps } from 'gatsby';
import { Button, Container } from 'react-bootstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEtsy, faFacebookF, faInstagramSquare, faPinterestP } from '@fortawesome/free-brands-svg-icons';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

import meImage from '../images/me.png';
import './about-me.css';

const AboutMePage: React.FC<PageProps> = () => {
    return (
        <>
            <Navigation />
            <main>
                <Container className='flex-fill d-flex justify-content-center p-5'>
                    <div className='row'>
                        <div className='px-2 col-12 col-md-7 col-lg-5 col-xl-4'>
                            <img src={meImage} />
                        </div>
                        <div className='px-2 col-12 col-md-5 col-lg-7 col-xl-8'>
                            <h1>About me</h1>
                            <h3>Hi! My name is Jinny Park :)</h3>
                            <p>Hi everyone! I’m a sole proprietor, and every item you see here is handmade by me with care and creativity. Because each piece is crafted by hand, no two are ever exactly alike !</p>
                            <p>Every creation has its own unique charm. I love designing new, beautiful items that bring joy and a personal touch to every customer’s experience!</p>

                            <div className="justify-content-start py-4">
                                <Link className='p-1' to='https://www.facebook.com/love2craftllc.2024/'>
                                    <Button variant='dark' size='sm' className='rounded-circle'>
                                        <FontAwesomeIcon icon={faFacebookF} />
                                    </Button>
                                </Link>
                                <Link className='p-1' to='https://www.pinterest.com/love2craftllc/'>
                                    <Button variant='dark' size='sm' className='rounded-circle'>
                                        <FontAwesomeIcon icon={faPinterestP} />
                                    </Button>
                                </Link>
                                <Link className='p-1' to='https://www.instagram.com/love2craftllc/'>
                                    <Button variant='dark' size='sm' className='rounded-circle'>
                                        <FontAwesomeIcon icon={faInstagramSquare} />
                                    </Button>
                                </Link>
                                <Link className='p-1' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                                    <Button variant='dark' size='sm' className='rounded-circle'>
                                        <FontAwesomeIcon icon={faEtsy} />
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </Container>
            </main>
            <Footer />
        </>
    )
}

export default AboutMePage

export const Head: HeadFC = () => <title>About Me</title>